import Vue from 'vue'
import Router from 'vue-router'

import FlexContainer from "../components/FlexContainer";
import DraggableContainer from "../components/DraggableContainer";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FlexContainer',
      component: FlexContainer
    }
  ]
})



