<template>
  <div id="app">
    <v-flex-container direction="column">
      <v-header></v-header>
      <v-app-nav></v-app-nav>
      <v-flex-container direction="row">
        <v-flex-child class="child1" flex="1">
          text in 1
        </v-flex-child>
        <v-flex-child class="child2" flex="1">
          text in 2
        </v-flex-child>
      </v-flex-container>
      <v-footer></v-footer>
    </v-flex-container>
  </div>
</template>

<script>
import FlexContainer from "./components/FlexContainer";
import FlexChild from "./components/FlexChild";
import DraggableContainer from "./components/DraggableContainer";
import FlexNav from "./components/FlexNav";
import AppNav from "./components/AppNav";
import Header from "./components/Header";
import Footer from "./components/Footer";


export default {
  name: "App",
  components: {
    "v-flex-container": FlexContainer,
    "v-flex-child": FlexChild,
    "v-draggable-container": DraggableContainer,
    "v-app-nav":AppNav,
    "v-footer": Footer,
    "v-header": Header,
  }
};
</script>

<style>
.child2 {

}

.child1 {

}

#app {
  display: flex;
}
</style>



