<template>
  <div class='wrapper'>
    <muuri-grid id="example-grid">
      <item-sm id="item-sm" width="100%" height="120px">
        <item-link id="link" text="Example Link" route="/"></item-link>
        <p>Small item.</p>
      </item-sm>
      <item-md id="item-md" width="220px" height="100%">
        <p>Medium item.</p>
      </item-md>
      <item-lg id="item-lg" width="720px" height="100%">
        <p>Large item.</p>
      </item-lg>
    </muuri-grid>
  </div>
</template>

<script>
// https://jonsuh.com/hamburgers/
import Vue from 'vue'
import VueMuuri from 'vue-muuri'
// You need a specific loader for CSS files like https://github.com/webpack/css-loader
import 'vue-muuri/dist/vue-muuri.css'
import { ItemLink, ItemSm, ItemMd, ItemLg } from 'vue-muuri'

Vue.use(VueMuuri)
export default {
  name: "flexnav",
  components: {
    "item-link":ItemLink, "item-sm":ItemSm, "item-md":ItemMd, "item-lg":ItemLg
  },
  data() {
    return {
      msg: ""
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>



</style>



